@import url(https://fonts.googleapis.com/css2?family=Archivo:ital,wght@0,700;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;font-size:16px;scroll-behavior:smooth}

.profile{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding-top:1.5rem;flex-direction:column}.profile .content-container{display:flex;justify-content:center;height:90%}.profile .arrow-down{color:#a68500;margin-bottom:1.25rem}.profile-picture{display:none;margin-right:3rem}.profile-components{height:36.3rem;padding:1rem;border:solid}.info-container{display:flex;flex-direction:column;width:80%;margin-left:0rem}.jumbotron.resume-mode{padding:none}.modes{display:flex;flex-direction:column;justify-content:center;align-content:flex-end;padding-top:1rem}.modes a{color:#fff}.modes button{margin-bottom:0.25rem;background-color:#a68500;color:#fff}.modes button:hover{border:solid black;background-color:#a68500}.modes .nbsp{display:none}@media only screen and (min-width: 471px){.modes{flex-direction:row}.modes .nbsp{display:inline;display:initial}}@media only screen and (min-width: 500px){.modes{padding-top:1rem}}@media only screen and (min-width: 520px){.info-container{height:auto}}@media only screen and (min-width: 600px){.class-separator{display:none}.Summary p{font-size:0.93rem}.Summary li{font-size:0.93rem}}@media only screen and (min-width: 700px){.info-container{width:60%}}@media only screen and (min-width: 815px){.profile-picture{display:block}.info-container{width:50%}}@media only screen and (min-width: 1050px){.profile-components{padding:1rem;border:solid}}@media only screen and (min-width: 1000px){.info-container{width:40%}}@media only screen and (min-width: 1215px){.profile{height:100vh;padding-top:1.5rem}.profile .arrow-down{margin-bottom:1.25rem}.profile-components{height:77%;padding:1rem}.info-container{display:flex;flex-direction:column}}

.Summary{padding:0 0 1.25rem}.Summary .break{display:none}.Summary ul{text-align:left;-webkit-animation-name:text;animation-name:text;-webkit-animation-duration:2s;animation-duration:2s}.Summary .supplementary{display:none}.Summary h4{-webkit-animation-name:title;animation-name:title;-webkit-animation-duration:1.5s;animation-duration:1.5s}.Summary p{text-align:justify;-webkit-animation-name:text;animation-name:text;-webkit-animation-duration:2s;animation-duration:2s}@-webkit-keyframes title{0%{opacity:0}100%{opacity:100}}@keyframes title{0%{opacity:0}100%{opacity:100}}@-webkit-keyframes text{0%{opacity:0}100%{opacity:100}}@keyframes text{0%{opacity:0}100%{opacity:100}}@media only screen and (min-width: 528px){.Summary .break{display:block}}@media only screen and (min-width: 471px){.Summary .supplementary{display:block}}

.skills{padding-bottom:1.25rem}.upper-half,.bottom-half{display:flex;justify-content:space-evenly;padding-bottom:1rem}.bottom-half{padding-bottom:0em}.testing,.soft-skills .languages{display:none}.front-end,.back-end,.testing,.programming-languages,.soft-skills{line-height:0.5}h5{line-height:2;-webkit-animation-name:skills-titles;animation-name:skills-titles;-webkit-animation-duration:1.5s;animation-duration:1.5s}.skills p{-webkit-animation-name:skills-text;animation-name:skills-text;-webkit-animation-duration:2s;animation-duration:2s}@-webkit-keyframes skills-titles{0%{opacity:0}100%{opacity:100}}@keyframes skills-titles{0%{opacity:0}100%{opacity:100}}@-webkit-keyframes skills-text{0%{opacity:0}100%{opacity:100}}@keyframes skills-text{0%{opacity:0}100%{opacity:100}}@media only screen and (min-width: 515px){.testing,.soft-skills .languages{display:block}}

.proProjects h2{font-size:1.5rem;padding-bottom:1rem}.proProjects h4{font-size:1rem;font-weight:700}.proProjects div{text-align:left;padding-bottom:0.5rem}

.Head{margin:0px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#a68500;background-image:url("https://www.transparenttextures.com/patterns/arches.png")}.Head .down-button{display:none;bottom:5%;position:absolute;width:100%}.links{display:flex;justify-content:flex-end;color:white;padding-top:1em;padding-right:2em}.links a:nth-child(1){color:white;margin-right:1em}.links a:nth-child(2){color:white}.links a:nth-child(3){color:white;margin-left:1em}.Introduction-text{padding:5rem 0 40vh 2.25rem;font-size:1.75rem;font-family:'Archivo', sans-serif}.Introduction-text p{margin-bottom:0}.name{display:flex;color:white;-webkit-animation-name:fade-in-text;animation-name:fade-in-text;-webkit-animation-duration:1.25s;animation-duration:1.25s;padding-bottom:3rem}.description{display:flex;flex-direction:column;font-family:'Archivo', sans-serif}.description p{display:none;line-height:1}.description p:nth-child(1){display:flex;color:white;-webkit-animation-name:fade-in-text;animation-name:fade-in-text;-webkit-animation-duration:2s;animation-duration:2s}.description p:nth-child(2){display:flex;color:white;-webkit-animation-name:fade-in-text;animation-name:fade-in-text;-webkit-animation-duration:2.75s;animation-duration:2.75s}.description p:nth-child(3){display:flex;color:white;-webkit-animation-name:fade-in-text;animation-name:fade-in-text;-webkit-animation-duration:3.5s;animation-duration:3.5s}span{display:flex;justify-content:center}@-webkit-keyframes fade-in-text{0%{opacity:0}90%{opacity:0}95%{opacity:0.5}100%{opacity:1}}@keyframes fade-in-text{0%{opacity:0}90%{opacity:0}95%{opacity:0.5}100%{opacity:1}}@media only screen and (min-width: 768px){.Introduction-text{display:flex;flex-direction:column;padding:7rem 7rem 10rem 7rem;font-size:3em}}@media only screen and (min-height: 600px){.Head{height:100vh}.Head .down-button{display:block}}

span .arrow-down{opacity:0}span .arrow-down{display:flex;color:white;-webkit-animation-name:blinking-arrow;animation-name:blinking-arrow;-webkit-animation-duration:1.75s;animation-duration:1.75s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-delay:4s;animation-delay:4s}.arrow-down{color:white}@-webkit-keyframes blinking-arrow{0%{opacity:0}25%{opacity:0.25}50%{opacity:0.5}75%{opacity:0.65}100%{opacity:1}}@keyframes blinking-arrow{0%{opacity:0}25%{opacity:0.25}50%{opacity:0.5}75%{opacity:0.65}100%{opacity:1}}

.Projects{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#a68500;background-image:url("https://www.transparenttextures.com/patterns/arches.png");padding-top:2em}.Projects h4{margin-top:0.5em;font-family:'Archivo', sans-serif;font-size:2em;color:white}.websites{display:flex;justify-content:center;-webkit-animation-name:website;animation-name:website;-webkit-animation-duration:10s;animation-duration:10s;height:80%}.project-img{-webkit-animation-name:website;animation-name:website;-webkit-animation-duration:2s;animation-duration:2s}.toggle-mode-arrows{color:white}.placeholder{color:white;cursor:pointer}.projects-text{display:flex;justify-content:center}@-webkit-keyframes website{0%{opacity:0}100%{opacity:1}}@keyframes website{0%{opacity:0}100%{opacity:1}}@media only screen and (min-height: 580px){.Projects{height:100vh}}

.project{width:80%;display:flex;text-align:justify;flex-direction:column;align-content:flex-start;color:white}.project-description{margin-bottom:0.5em;padding-top:1em}.project-img{width:100%}@media only screen and (min-width: 1024px){.project{width:60%}}

